<style scoped>

.fourth {
  --fourth-w: var(--main-width);
  width: var(--fourth-w);
  min-width: var(--main-width);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.fourth > div {
  margin: 15px 0 20px;
}

.fourth div.left {
  width: 234px;
  height: 170px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #5f5750;
  color: rgba(243, 243, 243, 0.6);
  /*overflow: hidden;*/
}

.fourth div.left div.iconfont {
  font-size: 12px;
  width: calc(230px / 3 - 1px);
  height: calc(170px / 2 - 1px);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.fourth div.left a {
  position: relative;
  margin: 2px 0 0 2px;
}

.fourth div.left a:hover {
  color: white;
}

.fourth div.left a:not(:nth-of-type(3n+1))::before {
  position: absolute;
  top: 18px;
  /*left: 1px;*/
  content: "";
  display: inline-block;
  width: 1px;
  height: 50px;
  background-color: rgba(153, 153, 153, 0.1);
}

.fourth div.left a:nth-of-type(-n+3)::after {
  position: absolute;
  left: 39px;
  top: 55px;
  content: "";
  display: inline-block;
  width: 1px;
  height: 60px;
  rotate: 90deg;
  background-color: rgba(153, 153, 153, 0.1);
}

.fourth div.left div.iconfont::before {
  font-size: 24px;
}

.fourth div.right a:hover img {
  box-shadow: 1px 2px 15px rgba(0, 0, 0, .1);
}

.fourth div.right img {
  width: 315px;
  height: 170px;
  object-position: center;
  object-fit: cover;
}

</style>

<template>

  <div class="fourth">
    <div class="left">
      <a :href="href" v-for="{ name,href,class:clazz } in iconList">
        <div class="iconfont" :class="clazz">{{ name }}</div>
      </a>
    </div>
    <div class="right" v-for="{src,href} in imgList">
      <a :href="href"><img :src="src" alt=""></a>
    </div>
  </div>
</template>

<script>

// 加载横幅左边icon
import {getFourthImg, getIconFourth} from "@api/api";

export default {
  data() {
    return {
      iconList: [],
      imgList: [],
    }
  },
  mounted() {
    getIconFourth()
        .then(({data}) => {
          this.iconList = data;
        });
    getFourthImg()
        .then(({data}) => {
          this.imgList = data;
        });
  }
}
</script>
