<style scoped>


.menu .menu-item {
  vertical-align: middle;
  display: inline-block;
  width: 234px;
  line-height: 41px;
  font-size: 15px;
  color: rgba(255, 255, 255, 0.8);
  font-weight: normal;
  padding: 0 20px 0 30px;
}

.menu .menu-item:hover {
  background-color: #fd6600;
  font-weight: normal;
}


.menu .menu-item:hover ~ .menu-item-page {
  visibility: visible;
}

.menu .menu-item i {
  float: right;
  font-size: 25px;
  font-weight: normal;
}

.menu .menu-item-page {
  --page-one-col: 248px;
  --pageWidth: var(--page-one-col);
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 234px;
  height: var(--carousel-h);
  width: var(--pageWidth);
  min-width: var(--page-one-col);
  background-color: white;
  box-shadow: 0 1px 10px #ccc;

  display: flex;
  flex-flow: column wrap;
  align-items: center;
}

.menu .menu-item-page:after {
  content: "";
  overflow: auto;
  clear: both;
}

.menu .menu-item-page:hover {
  visibility: visible;
}

.menu .menu-item-page .box {
  /*display: inline-block;*/
  box-sizing: border-box;
  width: var(--page-one-col); /* 248px */
  height: calc(var(--carousel-h) / 6); /* 76px */
  padding: 18px 20px;
}

.menu .menu-item-page .box:hover span {
  color: #ff6700;
}

.menu .menu-item-page .box img {
  width: 40px;
  height: 40px;
  object-fit: cover;
  object-position: center center;

}

.menu .menu-item-page .box span {
  font-size: 15px;
  display: inline-block;
  margin: 0 10px;
  width: 148px;
  height: 40px;
  line-height: 40px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

</style>

<template>
  <li>
    <a @mouseover="$emit('msMenuItemParent', pick)" class="menu-item" :data-pick="pick" href="javascript:void(0)">{{
        name
      }}
      <i class="iconfont i-arrow_right"></i>
    </a>
    <div class="menu-item-page" :style="{ '--pageWidth': menuPick.width +'px'}">
      <a :href="href" v-for="{href,src,name} in menuPick.list">
        <div class="box">
          <img :src="src" alt="">
          <span>{{ name }}</span>
        </div>
      </a>
    </div>
  </li>
</template>


<script>
export default {
  props: [
    "pick", "name",
    "menuPick"
  ]
}
</script>