<style scoped>

.right-fixed {
  position: fixed;
  z-index: 999;
  right: 0;
  bottom: 15%;
  display: flex;
  flex-flow: column nowrap;
}

.right-fixed .fixed-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 84px;
  height: 92px;
  font-size: 13px;
  color: #757575;
  background-color: #fff;
  border: 1px solid rgba(204, 204, 204, 0.2);
}

.right-fixed .fixed-box:hover {
  color: #ff6700;
}

.right-fixed .fixed-box.mobile {
  position: relative;
}

.right-fixed .fixed-box.mobile div.qrCode {
  display: none;
  width: 130px;
  height: 192px;
  position: absolute;
  top: 0;
  right: 100px;
  text-align: center;
  padding-top: 20px;
  background-color: #ffffff;
  box-shadow: 0 1px 20px -5px #999999;
}

.right-fixed .fixed-box.mobile div.qrCode:after {
  content: "";
  font-size: 0;
  position: absolute;
  overflow: hidden;
  top: 21%;
  right: -18px;
  border-width: 10px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #fff;

  /* 旋转45°一样可以，这种有边框
  right: -10px;
  width: 20px;
  height: 20px;
  background-color: #fff;
  transform: rotate(45deg);
  border: 1px solid;
  border-color: black black transparent transparent;*/
}

.right-fixed .fixed-box.mobile div.qrCode img {
  width: 100px;
  height: 100px;
  object-fit: contain;
}

.right-fixed .fixed-box.mobile div.qrCode p {
  margin: 15px 20px;
  color: #757575;
}

.right-fixed .fixed-box.mobile:hover div.qrCode {
  display: block;
}

.right-fixed .fixed-box .iconfont {
  font-size: 36px;
}

.right-fixed .fixed-box span {
  margin-top: 4px;
}

.right-fixed .fixed-box.back-top {
  display: none;
  margin-top: 20px;
}

.right-fixed .back-top.active {
  display: flex;
}

@media screen and (max-width: 1306px) {
  .right-fixed .fixed-box {
    width: 27px;
    height: 42px;
    font-size: 0;
  }

  .right-fixed .fixed-box:hover {
    color: #ff6700;
  }


  .right-fixed .fixed-box .iconfont {
    font-size: 20px;
  }

  .right-fixed .fixed-box span {
    display: none;
  }
}
</style>
<template>
  <div class="right-fixed">
    <a class="fixed-box mobile" href="javascript:void(0)">
      <i class="iconfont i-phone"></i>
      <span>手机APP</span>
      <div class="qrCode">
        <img src="//resource.lwrong.com/public/img/xiaomi/qrcode_xiaomi_600.png" alt="">
        <p>扫码领取本页面地址</p>
      </div>
    </a>
    <a class="fixed-box" href="javascript:void(0)">
      <i class="iconfont i-person"></i>
      <span>个人中心</span>
    </a>
    <a class="fixed-box" href="javascript:void(0)">
      <i class="iconfont i-fix"></i>
      <span>售后服务</span>
    </a>
    <a class="fixed-box" href="javascript:void(0)">
      <i class="iconfont i-kefu"></i>
      <span>人工客服</span>
    </a>
    <a class="fixed-box" href="javascript:void(0)">
      <i class="iconfont i-car"></i>
      <span>购物车</span>
    </a>
    <a class="fixed-box back-top" :class="{active:isActive}" @click="backToTop($event)" href="javascript:void(0)">
      <i class="iconfont i-top"></i>
      <span>回顶部</span>
    </a>
  </div>
</template>
<script>

class Throttle {//掐住喉咙，间隔duration放一次
  #param //记录最后一次参数，到时间就调用
  #timer = null;
  #this;

  constructor(func, durationMillSecond, that) {
    if (!Number.isInteger(durationMillSecond)) {
      throw new TypeError("new Throttle(param) with a no number param");
    }
    this.func = func;
    this.duration = durationMillSecond;
    this.#this = that;
  }

  preRun(...param) {
    this.#param = param;
    if (this.#timer !== null) {
      return
    }
    this.#timer = setTimeout(() => {
      this.func.apply(this.#this, [...this.#param]);
      this.#timer = null;
    }, this.duration);
  }

  setThis(aThis) {
    this.#this = aThis;
    return this;
  }
}

function switchBackTop(showBackTop) {
  data.isActive = showBackTop;
}

let scrollThrottle = new Throttle(switchBackTop, 500);

function listenScroll(func, window, target = window) {/* 滚动窗口监听，浏览器窗口不要传target */
  const scrollTarget = target === window ? document.querySelector("html") : target;
  target.addEventListener("scroll", () => func(scrollTarget));
}

let data = {
  isActive: false,
};
export default {
  data() {
    return data
  },
  methods: {
    backToTop(e) {
      e.preventDefault();
      window.scroll({
        top: 0,
        // left: 0,
        behavior: "smooth"
      });
    }
  },
  mounted() {
    listenScroll(scrollTarget => {
      let showBackTop = scrollTarget.scrollTop > window.innerHeight / 2;
      scrollThrottle.preRun(showBackTop);
    }, window);

  }
}
</script>