<style scoped>

.second {
  min-width: var(--main-width);
  position: relative;
}

.header {
  margin: 0 auto;
  width: var(--main-width);
  text-align: right;
  white-space: nowrap;
}

.header a.logo {
  float: left;
  display: inline-block;
  height: 55px;
  width: 55px;
  margin: 22px 0 0 10px;
  background: url("//resource.lwrong.com/public/img/xiaomi/logo-mi.png") 0 0/100% no-repeat;
  background-clip: border-box;
  overflow: hidden;
  padding-top: 55px;
}

.header ul {
  float: right;
}

.header ul li {
  vertical-align: middle;
  display: inline-block;
}

.header ul li a {
  font-size: 16px;
  display: inline-block;
  line-height: 100px;
  padding: 0 10px;
  color: rgba(0, 0, 0, 0.8);
}

.header ul li > a:hover {
  color: rgba(255, 0, 0, 0.7);
}

.header ul li.detail {
  visibility: hidden;
  position: absolute;
  z-index: 10;
  height: 0;
  overflow: hidden;
  width: 100%;
  top: 100px;
  left: 0;
  border-top: 1px solid rgba(204, 204, 204, 0.5);
  box-shadow: 0 10px 15px -5px rgba(0, 0, 0, 0.1);
  background-color: white;
}

.header ul li:not(:last-of-type):hover ~ li.detail {
  visibility: visible;
  height: 220px;
  transition: height .5s;
}

.header ul li.detail:hover {
  visibility: visible;
  height: 220px;
}

.header ul li.detail > div:first-child {
  width: var(--main-width);
  margin: 0 auto;
}

.header ul li.detail > div a {
  position: relative;
  margin: 30px 10px 20px 10px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;

}

.header ul li.detail > div a:not(:last-of-type):after {
  position: absolute;
  top: calc(50% - 20px);
  right: -10px;
  translate: 0 -50%;
  display: inline-block;
  content: "";
  width: 1px;
  height: 100px;
  border-right: 1px solid rgba(204, 204, 204, 0.5);
}

.header ul li.detail > div a img {
  width: 180px;
  height: 120px;
  margin: 0 0 10px 0;
  object-fit: cover;
  object-position: center center;
}

.header ul li.detail > div a div {
  display: block;
  max-width: 150px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 12px;
  line-height: 20px;
  height: 20px;
}

.header ul li.detail > div a div:last-of-type {
  color: #fd6600;
}


.header li.search {
  position: relative;
  margin-left: 50px;
}

.header li.search input {
  vertical-align: middle;
  /*margin-left: 60px;*/
  padding-inline: 8px;;
  width: 250px;
  height: 44px;
  text-decoration: none;
  border: 1px solid #ccc;
  box-sizing: border-box;
  outline-style: none;
  border-radius: 0;
}

.header li.search button {
  vertical-align: middle;
  font-size: 24px;
  width: 44px;;
  height: 44px;
  outline-style: none;
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 0;
  background-color: transparent;
  border-left: none;

}

.header li.search button:hover {
  background-color: #fd6900;
  border: 1px solid #fd6900;
  cursor: pointer;
}

.header li.search input:hover {
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.header li.search input:focus {
  border: 1px solid rgba(255, 0, 0, 0.7);
}

.header li.search input:focus ~ div.tips {
  visibility: visible;

}

.header ul li div.tips {
  white-space: initial;
  visibility: hidden;
  display: block;
  position: absolute;
  z-index: 20;
  width: 250px;
  /*height: 0;*/
  top: 44px;
  left: 0;
  text-align: left;
  border: 1px solid rgba(255, 0, 0, 0.7);
  border-top: none;
  background-color: white;
}

.header ul li div.tips ul li {
  font-size: 14px;
  line-height: 30px;
  width: 250px;
  text-indent: 10px;
}

.header li.search input:hover + button {
  border: 1px solid rgba(0, 0, 0, 0.4);
  border-left: none;
}

.header li.search input:focus + button {
  border: 1px solid rgba(255, 0, 0, 0.7);
  border-left: none;
}
</style>

<template>
  <div class="second clear-after">
    <div class="header">
      <a href="#" class="logo"><h1>关键词：小米</h1></a>
      <ul>
        <li><a href="javascript:void(0)">Xiaomi手机</a></li>
        <li><a href="javascript:void(0)">Redmi手机</a></li>
        <li><a href="javascript:void(0)">电视</a></li>
        <li><a href="javascript:void(0)">笔记本</a></li>
        <li><a href="javascript:void(0)">平板</a></li>
        <li><a href="javascript:void(0)">家电</a></li>
        <li><a href="javascript:void(0)">路由器</a></li>
        <li><a href="javascript:void(0)">服务中心</a></li>
        <li><a href="javascript:void(0)">社区</a></li>
        <li class="detail">
          <div v-if="flag">
            <a :href="href" v-for="{href,src,name,price} in dataList">
              <img :src="src" alt="">
              <div>{{ name }}</div>
              <div>{{ price }}</div>
            </a>
          </div>
        </li>
        <li class="search">
          <form action="#">
            <label for="search"></label>
            <input id="search" type="text" name="search" autocomplete="off" placeholder="手机">
            <button onclick="return false;" class="iconfont i-search"></button>
            <div class="tips">
              <ul>
                <li>手机换新</li>
                <li>空调</li>
                <li>笔记本</li>
                <li>电视</li>
                <li>洗衣机</li>
              </ul>
            </div>
          </form>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import {getHeaderDetail} from "@api/api.js";

export default {
  data() {
    return {
      flag: false,
      dataList: []
    }
  },
  mounted() {
    getHeaderDetail()
        .then(({data}) => {
          this.dataList = data;
          this.flag = true;
        });
  }
}
</script>
