<style scoped>

.main {
  background-color: #f3f3f3;
  margin-top: 5px;
  min-width: var(--main-width);
}

.fifth {
  max-width: var(--main-width);
  margin: 25px auto;
}

.fifth img {
  width: var(--main-width);
  height: 120px;
}

.six {
  width: var(--main-width);
  margin: 0 auto 20px;
}

.six .six-block {
  float: left;
  display: inline-block;
  width: 234px;
  height: 614px;
  padding-bottom: 14px;
}

.six .six-block img {
  width: 234px;
  height: 614px;
}

.six .six-box {
  float: left;
  width: 234px;
  height: 300px;
  padding: 20px 0;
  margin-bottom: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.six .six-box:hover {
  transform: translate3d(0, -2px, 0);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.2s linear;
}

.six .six-box img {
  width: 160px;
  height: 160px;
}

.six .six-box span {
  user-select: none;
  text-align: center;
  display: block;
}

.six .six-box span:first-of-type {
  margin-top: 20px;
  font-size: 14px;
}

.six .six-box span:nth-of-type(2) {
  margin-top: 3px;
  font-size: 12px;
  color: #999999;
}

.six .six-box span:last-of-type {
  display: inline;
  margin-top: 10px;
  font-size: 12px;
  color: #fd6600;
}

.six .six-box span del {
  padding-left: 10px;
  color: #999999;
}

.six .six-box {
  margin-left: 14px;
}

</style>

<template>
  <div class="main clear-before clear-after">
    <div class="fifth">
      <a href="javascript:void(0)">
        <img src="//resource.lwrong.com/public/img/xiaomi/banner.png"
             alt="">
      </a>
    </div>
    <div class="six clear-after" v-for="(item,index) in repeat">
      <div class="six-block">
        <a href="">
          <img src="//resource.lwrong.com/public/img/xiaomi/six-left.png" alt="">
        </a>
      </div>
      <a :href="href" v-for="{src, name, description, price, noPrice='',href='javascript:void(0)'} in sixList">
        <div class="six-box">
          <img :src="src" alt="">
          <span>{{ name }}</span>
          <span>{{ description }}</span>
          <span>{{ price }} <span v-if="!!noPrice"><del>{{ noPrice }}</del></span> </span>
        </div>
      </a>

    </div>
  </div>
</template>

<script>
import {getSixbox} from "@api/api";

export default {
  data() {
    return {
      sixList: [],
      repeat: 3,
    }
  },
  mounted() {
    getSixbox()
        .then(({data}) => {
          this.sixList = data;
        })
  },
}
</script>
