<style scoped>
.third {
  --carousel-h: 460px;
  --carousel-w: var(--main-width);
  position: relative;
  width: var(--carousel-w);
  min-width: var(--main-width);
  margin: 0 auto;
}

.carousel {
  position: relative;
  width: var(--carousel-w);
  height: var(--carousel-h);
  margin: 0 auto;
  --carousel_btn-url: url("//resource.lwrong.com/public/img/xiaomi/icon-slides.png");
}

.carousel img.carousel-img {
  position: absolute;
  top: 0;
  left: 0;
  width: var(--carousel-w);
  height: var(--carousel-h);
  opacity: 0;
  transition: opacity 1s;
}

.carousel img.opacity1 {
  opacity: 1;
}

.carousel a[class*="carousel_btn_"] {
  position: absolute;
  top: 50%;
  translate: 0 -50%;
  width: 41px;
  height: 69px;
}

.carousel a[class*="carousel_btn_"]:hover {
}

.carousel .carousel_btn_left {
  left: 234px;
  background: var(--carousel_btn-url) -83px 0 no-repeat;
}

.carousel .carousel_btn_right {
  right: 0;
  background: var(--carousel_btn-url) -125px 0 no-repeat;
}

.carousel .carousel_btn_left:hover {
  background: var(--carousel_btn-url) no-repeat;
}

.carousel .carousel_btn_right:hover {
  background: var(--carousel_btn-url) -42px 0 no-repeat;
}

.carousel .dot-list {
  position: absolute;
  right: 50px;
  bottom: 30px;
  font-size: 0;
}

.carousel .dot-list .dot {
  display: inline-block;
  width: 7px;
  height: 7px;
  background-color: rgba(0, 0, 0, .4);
  box-sizing: content-box;
  border: 2px solid rgba(244, 244, 244, .6);
  border-radius: 50%;
  cursor: pointer;
  margin: 0 4px;
}

.carousel .dot-list .dot-selected {
  background-color: #fff;
  border: 2px solid rgba(0, 0, 0, .4);
}


.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 234px;
  background-color: rgba(115, 117, 119, 0.7);
  padding: 15px 0;
  height: var(--carousel-h);
  box-sizing: border-box;
}
</style>

<template>

  <div class="third">
    <div class="carousel">
      <a href="javascript:void(0)"><img class="carousel-img " :class="{'opacity1': imgN ===index}"
                                        v-for="({src}, index) in imgList" :src=src alt=""></a>
      <a href="javascript:void(0)" @click="clickBtn(-1)" class="carousel_btn_left"></a>
      <a href="javascript:void(0)" @click="clickBtn(1)" class="carousel_btn_right"></a>
      <div class="dot-list">
        <span @click="clickDot(0)" class="dot " :class="{'dot-selected': imgN ===0}"></span>
        <span @click="clickDot(1)" class="dot" :class="{'dot-selected': imgN ===1}"></span>
        <span @click="clickDot(2)" class="dot" :class="{'dot-selected': imgN ===2}"></span>
        <span @click="clickDot(3)" class="dot" :class="{'dot-selected': imgN ===3}"></span>
      </div>
    </div>
    <div class="menu">
      <ul>
        <MenuItem
            @msMenuItemParent="mouseoverMenuItem"
            :menuPick="menu[pick]"
            :pick = "pick"
            :name = "name"
            v-for="{pick,name} in menu.list"
        ></MenuItem>
      </ul>
    </div>
  </div>
</template>

<script>

import {getCarouselImg, getMenuLi, getMenuPageBoxProxy} from "@api/api";
import MenuItem from "./MenuItem";

function calcWidth(dataList) {
  const oneColWidth = 248;
  // console.log(getComputedStyle(menu_item_page).getPropertyValue(pageWidthVarName));
  let colNum = Math.floor((dataList.length - 1) / 6) + 1;
  // colNum = colNum > 0 ? colNum : 1; //min-width
  return colNum * oneColWidth;
}

//监听菜单hover
const menuRefreshLockMap = new Map();

export default {
  data() {
    return {
      imgN: 0,
      imgList: [],
      menu: [],
      pageItemFlag: false,
    }
  },
  components: {
    MenuItem
  },
  methods: {
    mouseoverMenuItem(pick) {
      if (menuRefreshLockMap.get(pick)) {
        return;//已经有过了，节流
      }
      this.menu[pick].list = [];
      getMenuPageBoxProxy(pick)
          .then(({data}) => {
            console.dir(data)
            this.menu[pick].list = data;
            this.menu[pick].width = calcWidth(data);

            menuRefreshLockMap.set(pick, true);
            setTimeout(() => menuRefreshLockMap.delete(pick), 1000 * 10);//节流
          });
    },
    clickDot(N) {
      this.imgN = N;
    },
    clickBtn(k) {
      let selectedIndex = this.imgN;
      selectedIndex = ((selectedIndex > 0 ? selectedIndex : selectedIndex + 4) + k) % 4;
      this.imgN = selectedIndex;
    },
  },
  mounted() {
    getCarouselImg()
        .then(({data}) => {
          this.imgList = data;
        });
    getMenuLi()
        .then(({data}) => {
          if (data instanceof Array) {
            let menu = {};
            data.forEach(item => {
              menu[item.pick] = {list: [], width: 0};
            });
            menu.list = data;
            this.menu = menu;
          }
        });
  }
}
</script>
